.book {
}

.book--current {
  width: calc(100% + 40px);
  // background: $white;
  // border: 2px solid $white;
  background: $white;
  padding: 0.33em 20px;
  // box-shadow: 2px 2px 30px rgba(0,0,0,0.3);

  @media screen and (min-width: 780px) {
    width: calc(100% + 30px);
  }
}

.book--past {
  &:after {
    width: calc(100% + 20px);
    height: 5px;
    margin-right: -20px;
    background: $white;
    margin-bottom: 2rem;
  }

  @media screen and (min-width: 780px) {
    &:after {
      width: 100%;
      margin-top: 2em;
      margin-right: -30px;
      margin-left: auto;
    }
  }
}

.book--suggested {
  margin: 0 auto;
}

.book__title {
  font-family: Montserrat, sans-serif;
  color: $black;
  text-transform: uppercase;
  letter-spacing: 1px;

  .book--past & {
    color: $white;
  }

  .book--suggested & {
    color: $pink;
  }

  .book--current & {
  }
}

.book__author {
  position: relative;
  font-size: 1rem;
  color: $black;

  &:before {
    content: '';
    position: absolute;
    top: -10px;
    left: -5px;
    width: 20px;
    height: 2px;
    background: $white;
  }

  .book--past & {
    color: $white;
    &:before {
      background: $white;
    }
  }

  .book--suggested & {
    color: $pink;
    &:before {
      left: 50%;
      background: $pink;
    }
  }

  .book--current & {
    color: $black;
  }
}

.book__cover {
  width: calc(100% + 40px);
  margin-left: -20px;

  .book--current & {
    box-shadow: 10px 10px 0 #f093fa;
    @media screen and (min-width: 530px) {
      width: 50%;
      float: left;
      margin-right: 20px;
      margin-left: 0;
    }
  }

  .book--past & {
    box-shadow: 10px 10px 0 $white;

    @media screen and (min-width: 780px) {
      width: calc(100% + 20px);
      margin-left: -30px;
    }
  }

  .favourite-books__book & {
    width: auto;
    margin-left: 0;
    margin-right: auto;
    max-height: 150px;
  }
}

.book__description {
  font-size: 1rem;
  color: $black;
  // text-shadow: 0.5px 0.5px 0px rgba(0,0,0,0.3);
}

.book--past .fill {
  display: inline-block;
  width: calc(100% + 40px);
  margin-left: -10px;
}
